@import url("https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@500;900&display=swap");
/*******************************/
/********** RESET CSS **********/
/*******************************/
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
/*******************************/
/************* ROOT ************/
/*******************************/
:root {
  /* colors */
  --clr-primary: hsl(192, 37%, 48%);
  --clr-primary--light: hsl(192, 90%, 77%);
  --clr-secondary: hsl(268, 34%, 53%);
  --clr-secondary--light: hsl(268, 100%, 86%);
  --clr-neutral-dark: hsl(239, 21%, 20%);
  --clr-neutral-medium: hsl(240, 10%, 57%);
  --clr-neutral-light: hsl(0, 0%, 98%);
  /* font stuff */
  --ff-primary: "Red Hat Display", sans-serif;
}

/*******************************/
/********** UTILS **********/
/*******************************/
.container {
  width: min(90%, 70rem);
  margin: 0 auto;
}

/*******************************/
/********** GLOBALS **********/
/*******************************/
body {
  font-family: var(--ff-primary);
}

/****** typography ******/
/* desktop fontsizes */
h1,
h2 {
  font-weight: 900;
  color: var(--clr-dark);
}

h1 {
  font-size: 2.5rem;
  line-height: 1;
}
@media (min-width: 48rem) {
  h1 {
    font-size: 3rem;
  }
}
@media (min-width: 75rem) {
  h1 {
    font-size: 4rem;
  }
}

h2 {
  font-size: 2rem;
  line-height: 1.1;
}
@media (min-width: 48rem) {
  h2 {
    font-size: 2.5rem;
  }
}
@media (min-width: 75rem) {
  h2 {
    font-size: 2.5rem;
  }
}

.overline {
  font-size: 1rem;
  font-weight: 900;
  line-height: 1.625;
  text-transform: uppercase;
  letter-spacing: 0.25rem;
  color: var(--clr-primary);
}
@media (min-width: 48rem) {
  .overline {
    font-size: 1rem;
  }
}
@media (min-width: 75rem) {
  .overline {
    font-size: 1rem;
  }
}

p,
.text {
  font-size: 1rem;
  font-weight: 500;
  line-height: 1.625;
  color: var(--clr-neutral-medium);
}
@media (min-width: 48rem) {
  p,
.text {
    font-size: 1rem;
  }
}
@media (min-width: 75rem) {
  p,
.text {
    font-size: 1.125rem;
  }
}

/****** buttons ******/
.btn, .btn-secondary, .btn-primary {
  display: inline-block;
  border: none;
  border-radius: 1.875rem;
  padding: 1rem 2.5rem;
  font-weight: 900;
  color: var(--clr-neutral-light);
}

.btn-primary {
  background-color: var(--clr-primary);
}
.btn-primary span {
  color: var(--clr-primary--light);
  padding-left: 0.25rem;
}
.btn-primary:hover, .btn-primary:focus {
  background-color: rgba(77, 150, 168, 0.7);
}

.btn-secondary {
  background-color: var(--clr-secondary);
}
.btn-secondary span {
  color: var(--clr-secondary--light);
  padding-left: 0.25rem;
}
.btn-secondary:hover, .btn-secondary:focus {
  background-color: rgba(132, 94, 176, 0.7);
}

.header img {
  margin: 0 auto;
  padding-block: 3rem 1.75rem;
}
@media (min-width: 48rem) {
  .header img {
    padding-block: 3rem 3.5rem;
  }
}
@media (min-width: 75rem) {
  .header img {
    padding-block: 5rem;
  }
}

.hero {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 2fr;
  grid-template-areas: "image" "hero-content";
}
@media (min-width: 48rem) {
  .hero {
    grid-template-rows: 1fr 1fr;
  }
}
@media (min-width: 75rem) {
  .hero {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    grid-template-areas: "image-left hero-content image-right";
  }
}
.hero__img {
  grid-area: image;
  align-self: center;
  position: relative;
  min-width: 110%;
  left: -5%;
}
@media (min-width: 75rem) {
  .hero__img {
    visibility: hidden;
    height: 0;
    margin-left: 0;
  }
}
.hero__img-left {
  visibility: hidden;
  height: 0;
  grid-area: image-left;
  position: relative;
}
@media (min-width: 75rem) {
  .hero__img-left {
    visibility: visible;
    height: auto;
    align-self: flex-start;
    min-width: 90%;
    left: -10%;
    top: -10%;
  }
}
.hero__img-right {
  visibility: hidden;
  height: 0;
  grid-area: image-right;
  position: relative;
}
@media (min-width: 75rem) {
  .hero__img-right {
    visibility: visible;
    height: auto;
    overflow: hidden;
    align-self: flex-end;
    min-width: 90%;
    right: -20%;
    top: 5%;
  }
}
.hero__content {
  grid-area: hero-content;
  text-align: center;
  width: 21rem;
  margin: 0 auto;
  margin-bottom: 2rem;
}
@media (min-width: 48rem) {
  .hero__content {
    width: 28rem;
    margin-top: 2rem;
    margin-bottom: 3.5rem;
  }
}
@media (min-width: 75rem) {
  .hero__content {
    align-self: flex-start;
    margin-top: 0;
    margin-bottom: 0;
    width: 34rem;
    margin-block: 1rem;
  }
}
.hero__title {
  display: block;
  margin-block: 1.5rem 0.5em;
  width: 10ch;
  margin: 0 auto;
}
.hero .btn-group {
  margin: 0 auto;
  margin-top: 0.75rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 48rem) {
  .hero .btn-group {
    flex-direction: row;
    align-items: flex-start;
    justify-content: center;
    gap: 1rem;
    margin-top: 1rem;
  }
}
.hero .btn-group .btn-primary, .hero .btn-group .btn-secondary {
  max-width: max-content;
  margin-top: 1rem;
}

.info {
  margin-top: 9rem;
}
@media (min-width: 75rem) {
  .info {
    margin-top: 12rem;
  }
}
.info__img-group {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.5rem;
  padding-top: 6rem;
}
.info__img-group img {
  border-radius: 8px;
}
@media (min-width: 48rem) {
  .info__img-group {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}
@media (min-width: 75rem) {
  .info__img-group {
    gap: 2rem;
  }
}
.info__content {
  text-align: center;
  margin: 0 auto;
  margin-top: 4rem;
}
.info__title {
  padding-block: 0.75em 1em;
  max-width: 14ch;
  margin: 0 auto;
}
@media (min-width: 48rem) {
  .info__title {
    max-width: 16ch;
  }
}
.info__text {
  margin: 0 auto;
}
@media (min-width: 48rem) {
  .info__text {
    max-width: 36rem;
  }
}
@media (min-width: 75rem) {
  .info__text {
    max-width: 32rem;
  }
}

.footer {
  background-image: linear-gradient(rgba(77, 150, 168, 0.9), rgba(77, 150, 168, 0.9)), url("/app/assets/mobile/image-footer.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  height: fit-content;
  text-align: center;
  color: var(--clr-neutral-light);
  margin-top: 12rem;
}
@media (min-width: 48rem) {
  .footer {
    background: linear-gradient(rgba(77, 150, 168, 0.9), rgba(77, 150, 168, 0.9)), url("/app/assets/tablet/image-footer.jpg");
  }
}
@media (min-width: 75rem) {
  .footer {
    background: linear-gradient(rgba(77, 150, 168, 0.9), rgba(77, 150, 168, 0.9)), url("/app/assets/desktop/image-footer.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    height: fit-content;
    text-align: start;
  }
}
.footer__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  padding-block: 5.25rem;
  padding-inline: 1.75rem;
}
@media (min-width: 75rem) {
  .footer__container {
    padding-block: 7rem;
    flex-direction: row;
    justify-content: flex-start;
    align-items: start;
    gap: 6rem;
  }
}
.footer__title {
  max-width: 12ch;
  flex: 3;
}
.footer__text {
  color: var(--clr-neutral-light);
  flex: 1;
}
@media (min-width: 48rem) {
  .footer__text {
    max-width: 32rem;
  }
}
@media (min-width: 75rem) {
  .footer__text {
    max-width: 100%;
  }
}
.footer .btn-secondary {
  max-width: max-content;
  flex: 3;
}
@media (min-width: 75rem) {
  .footer .btn-secondary {
    margin-top: 0;
    max-width: max-content;
  }
}

.circle-stripe {
  position: relative;
}
.circle-stripe .stripe {
  content: "";
  position: absolute;
  bottom: 28px;
  left: 50%;
  transform: translate(-50%);
  width: 1.25px;
  height: 84px;
  background-color: rgba(134, 134, 156, 0.25);
}
.circle-stripe .circle {
  content: "";
  position: absolute;
  top: -28px;
  left: 50%;
  transform: translate(-50%);
  width: 56px;
  height: 56px;
  background-color: white;
  border: 1px solid rgba(134, 134, 156, 0.25);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 900;
  color: var(--clr-neutral-medium);
}